/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Open Sans", sans-serif;
  margin: 0;
  margin-bottom: 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.w-100 {
  width: 100%;
}

.boton-verde {
  background-color: #D0E37F;
  color: #221D23;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border: none;
  border-radius: 1rem;
}
.boton-verde:hover {
  transition: ease 1s;
  background-color: #697b1b;
  cursor: pointer;
}

.boton-rojo {
  background-color: #DDB967;
  color: #221D23;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border: none;
  border-radius: 1rem;
}
.boton-rojo:hover {
  transition: ease 1s;
  background-color: #624b16;
  cursor: pointer;
}

.boton-verde-block {
  background-color: #D0E37F;
  color: #221D23;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border: none;
  display: block;
}
.boton-verde-block:hover {
  transition: ease 1s;
  background-color: #697b1b;
  cursor: pointer;
}

/** LAYOUT **/
.header a {
  color: #4F3824;
  font-weight: 700;
}

.contenido-header {
  margin-right: 10rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .contenido-header {
    min-height: 7rem;
  }
}

.logo {
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1);
  width: 40rem;
  margin-top: 0;
  margin-bottom: 0;
  transition: opacity 0.3s ease-in-out;
  opacity: 0.8;
}

.logo:hover {
  opacity: 1;
}

.footer {
  padding: 5rem 2.5rem;
  background-color: #221D23;
}
.footer .contenedor-footer {
  padding: 5rem 2.5rem;
}
.footer a {
  color: #faebd7;
  font-weight: 700;
}
.footer p {
  color: #faebd7;
  font-size: 1.7rem;
  margin: 2rem 0 0 0;
  text-align: center;
}

@media (min-width: 768px) {
  .navegacion-footer {
    display: flex;
    align-items: center;
  }
}
.navegacion-footer a, .navegacion-footer .newsletter-contenido {
  text-decoration: none;
  font-size: 2.5rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion-footer a, .navegacion-footer .newsletter-contenido {
    margin-right: 5rem;
    font-size: 2rem;
  }
  .navegacion-footer a:last-of-type, .navegacion-footer .newsletter-contenido:last-of-type {
    margin-right: 0;
  }
}

.formulario-newsletter p {
  color: #faebd7;
  font-size: 1.7rem;
  margin: 2rem 0 0 0;
  text-align: center;
}
.formulario-newsletter label {
  color: #faebd7;
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario-newsletter input:not([type=submit]) {
  padding: 1rem;
  display: block;
  border: 1px solid #4F3824;
  border-radius: 1rem;
  margin-bottom: 2rem;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion-header {
  display: none;
}
@media (min-width: 768px) {
  .navegacion-header {
    display: flex;
    align-items: center;
  }
}
.navegacion-header a {
  color: #221D23;
  text-decoration: none;
  font-size: 2.5rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion-header a {
    flex-direction: row;
    height: auto;
    margin-right: 5rem;
    font-size: 2rem;
  }
  .navegacion-header a:last-of-type {
    margin-right: 0;
  }
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 6rem;
}

.mostrar {
  display: block;
}

.navegacion-header a:hover {
  transform: scale(1.1, 1, 1);
}

.navegacion-header:hover > a:not(:hover) {
  transition: ease 0.5s;
  filter: blur(2px);
  transform: scale(0.9, 0.9);
}

.barra-sidebar {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .barra-sidebar {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion-sidebar {
  display: none;
  background-color: #221D23;
  padding: 20px; /* Espaciado interno */
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.5);
  transition: all 0.5s ease;
}
@media (min-width: 768px) {
  .navegacion-sidebar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    padding: 5px;
  }
}
.navegacion-sidebar a {
  margin-left: 5rem;
  margin-right: 5rem;
  color: #faebd7;
  font-weight: bold;
  text-decoration: none;
  font-size: 2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion-sidebar {
    flex-direction: row;
    justify-content: space-between;
    height: auto;
  }
}

.navegacion-sidebar a:hover {
  transition: ease-in-out 0.5s;
  color: #D4BE6A; /* Color de fondo al pasar el mouse */
}

@media (min-width: 768px) {
  .mobile-menu-sidebar {
    display: none;
  }
}
.mobile-menu-sidebar img {
  width: 6rem;
}

.mostrar {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .mostrar {
    flex-direction: row;
  }
}

.img-index {
  background-size: cover;
  background-position: center;
  backdrop-filter: blur(5px);
}

.main-banner {
  margin-bottom: 0 !important;
  background-color: #221D23;
  color: #faebd7;
  text-transform: uppercase;
  text-align: center;
}

.main-banner-blog {
  background-color: #221D23;
  color: #faebd7;
  text-transform: uppercase;
}

.banner-img img {
  max-height: 60rem;
  width: 100%;
  object-fit: cover;
  display: block;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
  color: #faebd7;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-articulos {
    background-image: url(/build/img/bg-articulos.jpg);
    background-size: cover;
    background-position: center;
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(3, 1fr);
  }
}
.banner-articulo {
  background-color: #faebd7;
  color: #221D23;
  border: 1px solid #9a9a9a;
  box-shadow: inset 0 0 0.5rem #221D23;
  margin-bottom: 2rem;
}

.imagen-articulo {
  margin: 0 auto;
  height: auto;
  display: block;
}

.articulo {
  display: block;
  background-color: #faebd7;
  margin: auto;
  height: auto;
  width: 75%;
  border: 1px solid #9a9a9a;
  border-radius: 1rem;
  box-shadow: inset 0 0 0.5rem #221D23;
}
.articulo .contenido-articulo {
  padding: 2rem;
}
.articulo h3 {
  font-weight: bold;
  font-size: larger;
  margin: 2rem;
}
.articulo p {
  font-size: medium;
}

.precio {
  color: #221D23;
  font-weight: bold;
  font-size: 3rem;
  text-align: center;
}

.contenedor-articulos .articulo:hover {
  transform: scale(1.1, 1, 1);
}

.contenedor-articulos:hover > .articulo:not(:hover) {
  transition: ease 0.5s;
  filter: blur(0.5px);
  transform: scale(0.9, 0.9);
}

.imagen-contacto {
  display: block;
  margin: 2rem auto;
  height: auto;
  width: 50%;
}
.imagen-contacto picture {
  justify-content: center;
}

.formulario p {
  font-size: 1.7rem;
  color: #4F3824;
  margin: 2rem 0 0 0;
}
.formulario fieldset {
  border-radius: 1rem;
}
.formulario legend {
  font-size: 2rem;
  color: #4F3824;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  border: 1px solid #4F3824;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.imagen-blog_banner {
  position: relative;
  overflow: hidden;
}

/* Estilo para cada artículo */
.entrada {
  display: flex;
  align-items: center;
  justify-content: space-between; /* Separa los elementos a cada lado */
  margin-bottom: 20px; /* Espacio entre artículos */
}

/* Estilo para las imágenes y el contenido de cada artículo */
.entrada__imagen, .entrada__contenido {
  flex: 1; /* Cada elemento toma la mitad del espacio disponible */
  padding: 10px; /* Espacio dentro de cada elemento */
}

.entrada--invertida {
  flex-direction: row-reverse;
}

.entrada--centrado {
  justify-content: center; /* Centra la imagen y el texto horizontalmente */
}

.entrada--centrado .entrada__contenido {
  text-align: left; /* Asegura que el texto esté alineado a la izquierda si es necesario */
  max-width: 50%; /* Opcional, limita el ancho del texto para mejorar la legibilidad */
}

/* Opcional: Añade media queries para ajustar el diseño en pantallas más pequeñas */
@media (max-width: 768px) {
  .entrada {
    flex-direction: column; /* Coloca los elementos en columna en pantallas pequeñas */
  }
}
.imagen-blog {
  display: block;
  margin: 2rem auto;
  height: auto;
  width: 80%;
}
.imagen-blog picture {
  justify-content: center;
}

.body-login {
  background-image: url(/build/img/login-bg.jpg);
}

/** PAGINAS INTERNAS **/
.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #221D23;
  font-weight: 900;
  text-transform: uppercase;
  margin: 5rem 0;
  border-radius: 1rem;
}
.alerta.error {
  background-color: #f76a6a;
}
.alerta.exito {
  background-color: green;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #D0E37F;
}
table.propiedades thead th {
  color: #faebd7;
  padding: 2rem;
}
table.propiedades .imagen-tabla {
  width: 10rem;
}

.imagen-small {
  width: 20rem;
}

.body-admin {
  background-color: #faebd7;
}

.banner-form {
  font-family: "Overlock", sans-serif;
  color: #D0E37F;
}

.banner {
  background-color: #221D23;
}